.Navigation {
  width: 90%;
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 5%;
  overflow: hidden;
  z-index: 500;

  transform: translateY(100%);
  opacity: 0;

  transition: opacity 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44),
    transform 0.8s cubic-bezier(0.1, 1.01, 0.56, 1.44);
  transition-delay: 2s;

  /* Layout */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.goDown {
  opacity: 1;
  transform: translateY(0);
}

.active {
  border: 2px solid var(--blue);
}
