.Side {
  height: 100vh;
  width: 25vw;
  position: fixed;
  top: 0;
  right: 0;
  background-color: var(--black);
  text-align: center;
  z-index: 500;

  /* Layout */
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.downSection {
  cursor: pointer;
  color: var(--white);
  opacity: 0;
  animation: goDown 0.5s ease 2s reverse forwards;
}

.downSection:hover .downIcon {
  transform: translateY(5px);
}

.downIcon {
  margin-top: 10px;
  transition: transform 0.2s ease;
}

.goDown {
  cursor: default;
  animation: goDown 0.7s ease-out forwards;
}

@keyframes goDown {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(20px);
  }
}
