.Work {
  height: 0;
  background: var(--blue);
  overflow: auto;
  position: relative;
  transition: height 0.5s ease, padding 0.5s ease;

  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.shown {
  height: 100%;
}

.slide {
  height: 100%;
  width: 100%;
  background-color: var(--dark-gray);
  position: absolute;
  top: 0;
  left: 0;
  transform-origin: 100% 100%;
  z-index: 9999;

  /* Layout */
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide h1 {
  font-size: 7rem;
  color: var(--light-gray);
}

.show {
  animation: go 1s cubic-bezier(0.24, -0.57, 1, -0.03) 1s forwards;
}

@keyframes go {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: rotate(45deg);
  }
}
