.IntroItems {
  width: 75vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  padding-left: 2%;
  transition: all 0.8s ease-out;

  /* Layout */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.IntroItems * {
  opacity: 0;
}

.IntroItems h3 {
  font-size: 2rem;
  font-weight: 400;
  animation: show 0.8s ease forwards;
}

.IntroItems h1 {
  font-size: 4rem;
  font-weight: 300;
  margin-top: 75px;
  animation: show 1s ease 0.9s forwards;
}

.IntroItems p {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--light-gray);
  margin-top: 20px;
  animation: show 0.9s ease 1.4s forwards;
}

.goDown {
  width: 25vw;
  left: 75vw;
  color: var(--white);
  animation: align-text 0.2s linear 1s forwards;
}

.goDown h3 {
  font-size: 1.2rem;
}

.goDown h1 {
  font-size: 2.7rem;
}

.goDown p {
  font-size: 1.1rem;
  color: var(--light-gray);
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes align-text {
  from {
    text-align: center;
    align-items: center;
  }
  to {
    text-align: left;
    align-items: flex-start;
  }
}
